import { configureStore } from '@reduxjs/toolkit';
import { counterSlice } from '../../features/contact/counterSlice';
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';
import { basketSlice } from '../../features/basket/basketSlice';
import { catalogSlice }  from '../../features/catalog/catalogSlice';
import { accountSlice } from '../../features/account/accountSlice';

//configureStore  redux toolkits  Slice/Reducer
export const store = configureStore({
    reducer:{
        counter : counterSlice.reducer,
        basket: basketSlice.reducer,
        catalog: catalogSlice.reducer,
        account: accountSlice.reducer,
    }
})


// Default  store  store.getState, store.dispatch ()
export type RootState = ReturnType<typeof store.getState>	//  state 
export type AppDispatch = typeof store.dispatch;			// dispatch  action
 
// dispatch  state ()
export const useAppDispatch = ()=>useDispatch<AppDispatch>()
export const useAppSelector : TypedUseSelectorHook<RootState> = useSelector
